import axios from 'axios';

// ajax 
import { HOST, commonParam } from './config';

// 
export function getSurveyList(option) {
  const url = HOST + '/weixin/survey/lists';

  const data = Object.assign({}, commonParam, option);

  return axios.post(url, data);
}

